import {Swiper, Navigation, EffectCoverflow} from 'swiper';
Swiper.use([Navigation, EffectCoverflow]);

import Micromodal from 'micromodal';

window.addEventListener('load', function () {
  
  
  /* Modal */
  Micromodal.init({
    openTrigger: 'data-micromodal-open',
    closeTrigger: 'data-micromodal-close',
    disableScroll: true,
    awaitOpenAnimation: true,
    awaitCloseAnimation: true
  });
  
  
  /* Sliders */
  const sliderWeddings = new Swiper('.slider-wd', {
    allowTouchMove: false,
    navigation: {
      nextEl: '.slider-container--wd .slider-navigation__next-btn',
      prevEl: '.slider-container--wd .slider-navigation__prev-btn',
    },
  });
  
  const sliderMediaPhoto = new Swiper('.slider-media--photo',  {
    initialSlide: 1,
    effect: "coverflow",
    coverflowEffect: {
      rotate: 0,
      stretch: 0,
      depth: 200,
      modifier: 1,
      slideShadows: false,
    },
    spaceBetween: 50,
    centerInsufficientSlides: true,
    centeredSlides: true,
    navigation: {
      nextEl: '#slider-photo-panel .slider-navigation__next-btn',
      prevEl: '#slider-photo-panel .slider-navigation__prev-btn',
    },
    breakpoints: {
      528: {
        coverflowEffect: {
          rotate: 0,
          stretch: 0,
          depth: 200,
          modifier: 1,
          slideShadows: false,
        },
        spaceBetween: 0,
        
      }
    }
  });
  
  const sliderMediaVideo = new Swiper('.slider-media--video', {
    initialSlide: 1,
    effect: "coverflow",
    coverflowEffect: {
      rotate: 0,
      stretch: 0,
      depth: 200,
      modifier: 1,
      slideShadows: false,
    },
    spaceBetween: 50,
    centerInsufficientSlides: true,
    centeredSlides: true,
    navigation: {
      nextEl: '#slider-video-panel .slider-navigation__next-btn',
      prevEl: '#slider-video-panel .slider-navigation__prev-btn',
    },
    breakpoints: {
      528: {
        coverflowEffect: {
          rotate: 0,
          stretch: 0,
          depth: 200,
          modifier: 1,
          slideShadows: false,
        },
        spaceBetween: 0,
        
      }
    }
  });
  
  const sliderReviews = new Swiper('.slider-reviews', {
    initialSlide: 1,
    slidesPerView: 1,
    centeredSlides: true,
    spaceBetween: 10,
    navigation: {
      nextEl: '.slider-container--reviews .slider-navigation__next-btn',
      prevEl: '.slider-container--reviews .slider-navigation__prev-btn',
    },
    breakpoints: {
      945: {
        initialSlide: 0,
        slidesPerView: 4,
        spaceBetween: 20,
        centeredSlides: false,
      },
      680: {
        initialSlide: 0,
        slidesPerView: 2,
        spaceBetween: 10,
        centeredSlides: false,
      }
    }
  });
  
  
  /* Tabs */
  let tabs = document.querySelector('.tabs');
  let tabsControl = tabs.querySelector('.tabs__controls');
  
  tabsControl.addEventListener('click', function (e) {
    e.preventDefault();
    let target = e.target;
    
    if (target.classList.contains('tabs__toggle')) {
      
      let targetTab = document.getElementById(target.dataset.tabTarget);
      let lastTab = tabs.querySelector('.tabs__panel--active');
      let lastToggle = tabs.querySelector('.tabs__toggle--active');
      
      if (targetTab === lastTab) {
        return;
      }
      
      lastToggle.classList.remove('tabs__toggle--active');
      lastTab.classList.remove('tabs__panel--active');
      
      target.classList.add('tabs__toggle--active');
      targetTab.classList.add('tabs__panel--active');
      
      /* Update slider */
      sliderMediaVideo.update();
      sliderMediaPhoto.update();
    }
  });
  
  
});


